
.root{
    display:inline-block;
    margin: 20px;
    margin-top: 30px;
    height: 350px;
    width: 270px;
}
.media-card{
    padding-top: 56.25%;
}
.root .span-card{
    font-size: 16px;
    font-family:'Arial';
    text-align: center;
    color: black;
}
.root .NavLink{

         text-decoration: none;
         color: black;
         font-size: 15px;
         font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
         text-align: center;
         margin: 10px;
         width: 100%;
         background: #F9EBEA;
        
   
     
}
.root .iconButton{
    right: 90px;
}
@media screen and (max-width:768px)
{
.root{
    display:flexbox;
    margin-top: 5px;
    height: 250px;
    width: 40%;
    margin: 0px;
    background-color: black;
    margin-right: 6%;
    margin-bottom: 10px;
}
.media-card {
    padding:25.5%
}
.root .span-card{
    font-size: 12px;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    color: black;
    height: 60px;
}
.root .iconButton{
    right: 20px;
}
.root .NavLink{

    text-decoration: none;
    color: black;
    font-size: 10px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    background: #7F8C8D;

   


}
.root .iconButton{
    right: 0px;
}

}