.ContactUs {
    margin-top: 0px;
    display: flex;
    width: 100%;
    height: 500px;
    justify-content: center;
    align-items: center;
}
.ContactUs .btn-ContactUs {
    display: flex;
    flex-direction: column;
    border: 2px solid whitesmoke;
    width:100%;
    height: 500px;
    justify-content: center;
    align-items: center;


}
.ContactUs .btn-ContactUs button{
    color: darkorange;
    border: 1px solid bisque;
    text-align: center;
    width: 20px;
    margin: 10px;
    
}
.ContactUs .btn-ContactUs .textFields {
    width: 60%;
}
.ContactUs .btn-ContactUs label {
    font-size: 20px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
@media screen and (max-width:900px){
    .ContactUs .btn-ContactUs .textFields {
        width: 300px;
    }
}