.AboutUs {
    height: 500px;
    width: 100%;
    position: relative;
}
.AboutUs  img {
	width: 500px;
    height: 500px;
    cursor: pointer;
}
.AboutUs .text-box{
    position:absolute;
    width: 150px;
    height: 50px;
    background:#B7E3E3;
    bottom: 450px;
    right: 400px;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 5px;
    text-align: center;
}
.AboutUs .text-box p {
    font-family: Arial, Helvetica, sans-serif;
}
